import React, { useEffect } from "react";
import { preLoaderAnim } from "../animations";
import './preloader.css'

const PreLoader = () => {
    useEffect(() => {
        preLoaderAnim();
    }, []);
    return (
        <div className="preloader">
            <div className="texts-container">
                <span>Developer, </span>
                <span>Curator, </span>
                <span>Apps/Websites.</span>
            </div>
        </div>
    );
};

export default PreLoader;